export const ROMA_LIST = [
  'M',
  'CM',
  'D',
  'CD',
  'C',
  'XC',
  'L',
  'XL',
  'X',
  'IX',
  'V',
  'IV',
  'I',
];

const ARABIC_LIST = [1000, 900, 500, 400, 100, 90, 50, 40, 10, 9, 5, 4, 1];

export default function arabic2Roma(number: number, type: string): string {
  const map = type === 'upper' ? ROMA_LIST : ROMA_LIST.map(item => item.toLowerCase());
  let ret = '';
  let num = number;
  for (let i = 0; i < map.length; i++) {
    if (num === 0) break;
    const y = Math.floor(num / ARABIC_LIST[i]);
    num -= ARABIC_LIST[i] * y;
    for (let m = 0; m < y; m++) {
      ret += map[i];
    }
  }
  return ret;
}
